@import "../../foundation/variables";

.p-product__slideShow {
    display: grid;
    width: 100%;
    border-bottom: #e3e8cf 1px solid;

    .slideShow__mainImage {
        max-width: 100%;
        max-height: 100%;
    }
    .slideshow__subImage {
        max-width: 100%;
        max-height: 100%;
    }

    @media #{$screen_xs} {
        grid-template-columns: repeat(5, minmax(30px, 75px));
        grid-gap: 5px;
        margin-bottom: 15px;
        padding-bottom: 10px;

        .slideShow__mainImage {
            grid-column: 1 / span 5;
            grid-row: 1;
        }
    }

    @media #{$screen_sm} {
        grid-template-columns: 1fr max(75px) max(75px);
        grid-template-rows: repeat(5, minmax(1fr, 75px));
        grid-gap: 8px;
        margin-bottom: 20px;
        padding-bottom: 20px;

        .slideShow__mainImage {
            grid-column: 1;
            grid-row: 1 / span 5;
        }
    }
}